import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';

export interface IMovieResponse {
  caption: string;
  imageUrl: string;
  details: string;
}

@Injectable({
  providedIn: 'root'
})
export class CinemaService {

  constructor(private http: HttpClient) { }

  readonly API_URL = 'http://localhost:13845/api/';

  getMovies(): any {
    return this.http.get(`${this.API_URL}movies`);
  }

  getMoviesLocal(): Observable<IMovieResponse[]> {
    return of([
      {
        caption: 'Жизнь и смерть Луи де Артарузо',
        imageUrl: 'https://avatars.mds.yandex.net/get-pdb/1533739/c6b9c92c-567f-4c8b-ac44-b4c9f1d64007/s1200',
        details: 'Смелый, но мрачный Луи отправляется на поиски возлюбленной на Великий Восток'
      } as IMovieResponse,
      {
        caption: 'Великий желтый полосатик',
        imageUrl: 'https://avatars.mds.yandex.net/get-pdb/1691218/b1de74e6-8084-4b77-805e-978a315e7295/s1200',
        details: 'Самый большой в мире желтый полосатик, но вместе с тем наивный и трогательный, выходит в высший свет Петербурга'
      } as IMovieResponse,
      {
        caption: 'Вечера в хуторе',
        imageUrl: 'https://avatars.mds.yandex.net/get-pdb/1515151/8744599d-761f-4aa7-ba90-7fd126a40576/s1200',
        details: 'Сага о правильных грамматических конструкциях в построениях географических направлений'
      } as IMovieResponse
    ]);
  }
}


