import { Component, OnInit } from '@angular/core';
import { CinemaService, IMovieResponse } from '../cinema.service';

@Component({
  selector: 'app-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.scss']
})
export class MoviesComponent implements OnInit {

  constructor(private server: CinemaService) { }

  movies: IMovieResponse[] = [];
  currentMovie = 0;

  ngOnInit() {
    this.server.getMoviesLocal().subscribe((res: IMovieResponse[]) => {
      this.movies = res;
    });
  }
}
